.metadatawithtags {
  margin-bottom: 2%; }

.logmetadata {
  padding: 1%;
  border-radius: 5px;
  background-color: white;
  -webkit-box-shadow: 5px 5px;
          box-shadow: 5px 5px; }

/* unvisited link */
.metadatawithtags a:link {
  color: black;
  text-decoration: none; }

/* visited link */
.metadatawithtags a:visited {
  color: black;
  text-decoration: none; }

.logmetadata:hover {
  background-color: lightgray;
  cursor: pointer; }

.waiter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; }

.note {
  color: white;
  text-align: center; }

.tag {
  background-color: lightgray;
  margin-right: 2px;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 2px;
  padding-right: 2px;
  border: 1px solid #CCCCCC;
  border-radius: 2px;
  font-size: 12px; }

.tag:hover {
  background-color: white; }

@media (min-width: 768px) {
  .logtitle {
    float: left;
    padding-left: 0.5%; }
  .logdatetime {
    float: right;
    padding-right: 0.5%; }
  .clearboth {
    clear: both; } }

@media (max-width: 767px) {
  .logtitle {
    text-align: center;
    padding-left: 0.5%; }
  .logdatetime {
    text-align: center;
    padding-right: 0.5%; }
  .tags {
    text-align: center; }
  .clearboth {
    clear: both; } }



