.aboutcontent {
  padding: 1%;
  border-radius: 5px;
  background-color: white;
  margin-bottom: 2%;
  text-shadow: 5px 5px lightgray;
  text-align: center; }

.aboutcontent a {
  display: block;
  margin: 2%; }

/* unvisited link */
.aboutcontent a:link {
  color: black;
  text-decoration: none; }

/* visited link */
.aboutcontent a:visited {
  color: black;
  text-decoration: none; }

.aboutcontent a:hover {
  background-color: lightgray; }



